export default {
	name: "foot",
	props: {
		// 是否展示tip提示(啊欧，快到底了哦)
		"btm": {
			type: Boolean,
			default: true
		},
		// 顶部tip提示（啊欧，快到底了哦）背景是否展示white 
		"color": {
			type: Boolean,
			default: false
		},
		// 是否使用定位布局
		"position": {
			type: Boolean,
			default: false
		},
		// 页面
		"show": {
			type: Boolean,
			default: false,
		},
		// 隐藏
		"hide": {
			type: Boolean,
			default: false,
		},
		// 隐藏二维码等等
		"tip": {
			type: Boolean,
			default: false
		},
		// lately
		"lately": {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			codeList: [],
			footInfo: {}
		}
	},
	created() {
		this.getFootInfo();
		this.getFootConfig();
	},
	methods: {
		getFootInfo() {
			this.$http.getFootInfo({}, res => {
				if (res.code == 1) {
					this.codeList = res.data.reverse();
				} else {
					this.$common.errorToShow(res.msg);
				}
			})
		},
		getFootConfig() {
			this.$http.getFootConfig({}, res => {
				if (res.code == 1) {
					this.footInfo = res.data;
				} else {
					this.$common.errorToShow(res.msg)
				}
			})
		}
	}
}
